.header {
    width: 100%;

    &_container {
        max-width: 1024px;
        height: 60px;
        margin: 0 auto;
        border-bottom: 1px solid #eee;
        padding: 0 .75rem;
    }

    nav {
        display: flex;
        align-items: center;
        justify-content: start;
        height: 100%;
        line-height: 1;

        a {
            text-decoration: none;
            color: #111;
        }
    }

    &_title {
        font-size: 28px;
    }
}